* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.center {
  width: 100%;
  padding: 0 15px;
}
@media (min-width: 1200px) {
  .center {
    margin: 0 auto;
    width: 1170px;
  }
}

.button {
  color: #fff;
  display: block;
  font-size: 24px;
  font-weight: 400;
  text-decoration: none;
  padding: 10px 30px;
  background-color: #22A39F;
  border-color: #1c8784;
  border-radius: 4px;
  text-align: center;
  width: 300px;
  margin-top: 30px;
  transition: background-color 0.5s, opacity 0.5s;
}
.button:hover {
  background-color: #1c8784;
}
.button:active {
  opacity: 30%;
}

.inner-page-title {
  color: #727878;
  font-size: 54px;
  font-weight: 400;
  margin-bottom: 40px;
  margin-top: 40px;
  text-align: center;
}

.content .box input {
  border: 1px solid #a0a0a0;
  background-color: #f5f5f5;
  width: 457px;
  height: 34px;
  padding-left: 10px;
  font-size: 20px;
  font-weight: 400;
  margin-bottom: 12px;
}

@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: normal;
  font-display: swap;
  src: url("../../fonts/opensans.woff2") format("woff2"), url("../../fonts/opensans.woff") format("woff");
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: bold;
  font-display: swap;
  src: url("../../fonts/opensansbold.woff2") format("woff2"), url("../../fonts/opensansbold.woff") format("woff");
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: lighter;
  font-display: swap;
  src: url("../../fonts/opensanslight.woff2") format("woff2"), url("../../fonts/opensanslight.woff") format("woff");
}
.page {
  height: 100%;
}

.page-body {
  margin: 0;
  padding: 0;
  min-height: 100%;
  background-color: #F3EFE0;
  color: #434242;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 24px;
  line-height: 1.64;
  font-weight: 400;
  min-height: 100%;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
  box-shadow: 0px 5px 15px rgba(0, 1, 1, 0.2);
}

.header {
  position: relative;
  width: auto;
  background-color: #22A39F;
  color: #fff;
  line-height: 1.2;
  min-height: 150px;
}
@media (max-width: 1199px) {
  .header--nav-opened {
    height: 80vh;
  }
}

.header-wrapper {
  display: flex;
  flex-direction: column;
  padding-top: 30px;
  padding-bottom: 30px;
  row-gap: 20px;
}
@media (max-width: 1199px) {
  .header-wrapper {
    padding-right: 70px;
  }
}
@media (min-width: 1200px) {
  .header-wrapper {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}

.header-logo {
  display: flex;
  flex-direction: column;
}

.header-logo p {
  font-size: 26px;
  font-weight: 300;
  color: #fff;
  margin: 0;
  padding: 0;
}

.header-logo h1 {
  margin: 0;
  padding: 0;
}

.header-toggle {
  position: absolute;
  background-color: transparent;
  border: 0;
  right: 15px;
  top: 20px;
  padding: 20px 0;
}
@media (min-width: 1200px) {
  .header-toggle {
    display: none;
  }
}

.header-toggle span {
  display: block;
  width: 40px;
  height: 3px;
  position: relative;
}

.header--nav-closed .header-toggle span {
  background-color: #fff;
}
.header--nav-closed .header-toggle span::before {
  content: "";
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  background-color: #fff;
  transform: translateY(-10px);
  transition: transform 0.4s;
}
.header--nav-closed .header-toggle span::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  transform: translateY(10px);
  transition: transform 0.4s;
}

.header--nav-opened .header-toggle span {
  background-color: transparent;
}
.header--nav-opened .header-toggle span::before {
  content: "";
  position: absolute;
  display: block;
  width: 100%;
  height: 3px;
  background-color: #fff;
  left: 0;
  transform: rotate(45deg);
  transition: transform 0.4s;
}
.header--nav-opened .header-toggle span::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 3px;
  background-color: #fff;
  transform: rotate(-45deg);
  transition: transform 0.4s;
}

@media (max-width: 1199px) {
  .header--nav-closed .navigation {
    display: none;
  }
}
.about-me {
  display: flex;
  flex-direction: column;
  gap: 40px;
  padding-top: 20px;
  padding-bottom: 20px;
}

.about-me-link {
  color: inherit;
  text-decoration: none;
  font-weight: 400;
  color: #727878;
}
.about-me-link:hover {
  text-decoration: underline;
}

.about-me-content {
  display: flex;
  flex-direction: column;
  gap: 40px;
}
@media (min-width: 1200px) {
  .about-me-content {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}

.about-me-image {
  align-self: center;
  width: 389px;
  height: 389px;
  border-radius: 50%;
  object-position: center;
  object-fit: cover;
  box-shadow: 0px 5px 15px rgba(0, 1, 1, 0.2);
}
@media (max-width: 659px) {
  .about-me-image {
    display: none;
  }
}

.about-me-text {
  line-height: 1.64;
  display: flex;
  flex-direction: column;
  padding-right: 40px;
  padding-left: 40px;
}
@media (min-width: 1200px) {
  .about-me-text {
    width: 700px;
  }
}

.about-me-text p {
  margin-bottom: 20px;
}

.about-me-title {
  color: #22A39F;
  font-size: 54px;
  font-weight: 400;
  text-align: center;
}

.portfolio {
  padding-top: 20px;
  padding-bottom: 20px;
  display: flex;
  flex-direction: column;
  gap: 40px;
}

.portfolio-title {
  color: #22A39F;
  font-size: 54px;
  font-weight: 400;
  text-align: center;
}

.portfolio-list {
  list-style: none;
  display: flex;
  flex-direction: column;
  row-gap: 40px;
}
@media (min-width: 1200px) {
  .portfolio-list {
    display: grid;
    grid-template-columns: minmax(auto, 575px) minmax(auto, 575px);
    gap: 40px;
  }
}

.portfolio-item-link {
  position: relative;
  height: 100%;
  color: #fff;
  display: block;
  font-size: 24px;
  font-weight: 400;
  text-decoration: none;
  overflow: hidden;
  border-radius: 4px;
  text-align: center;
  transition: background-color 2s, opacity 2s;
}
.portfolio-item-link::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #000;
  opacity: 0;
  z-index: 1;
}
.portfolio-item-link:hover::after {
  opacity: 0.2;
}

.portfolio-item-image {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.navigation {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
@media (min-width: 1200px) {
  .navigation {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}

.navigation-link {
  position: relative;
  display: inline-block;
  padding-top: 10px;
  padding-bottom: 10px;
  color: #fff;
  text-decoration: none;
  margin-left: 20px;
  font-size: 21px;
  font-weight: 700;
  transition: text-decoration 2s;
}
.navigation-link::after {
  position: absolute;
  content: "";
  height: 4px;
  width: 100%;
  background-color: #fff;
  top: 100%;
  left: 0;
  opacity: 0;
  transform: translateY(10px);
  transition: opacity 0.3s, transform 0.3s;
}
.navigation-link--current::after {
  opacity: 1;
  transform: translateY(0px);
}
.navigation-link:hover::after, .navigation-link:focus::after {
  opacity: 1;
  transform: translateY(0px);
}

.footer {
  background-color: #333;
  text-align: center;
  color: #a4a4a4;
  font-size: 20px;
  font-weight: 400;
}